"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  Home,
  UploadCloud,
  User,
  Users,
  Search,
  Settings,
  MountainIcon,
  Star,
  Briefcase,
  Wrench,
  MessageSquare,
} from "lucide-react";
import { cn } from "@/lib/utils";
import {
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from "@/components/ui/sidebar";

const navItems = [
  { href: "/", label: "", icon: Home, tooltip: "" },
  { href: "/upload", label: "", icon: UploadCloud, tooltip: "" },
  { href: "/profile", label: "", icon: User, tooltip: "" },
  { href: "/community", label: "", icon: Users, tooltip: "" },
  { href: "/search", label: "", icon: Search, tooltip: "" },
  { href: "/coaches", label: "", icon: Briefcase, tooltip: "" },
  { href: "/shoe-repair", label: "", icon: Wrench, tooltip: "" },
  { href: "/messages", label: "", icon: MessageSquare, tooltip: "" },
];

const secondaryNavItems = [
  { href: "/membership", label: "", icon: Star, tooltip: "" },
  { href: "/settings", label: "", icon: Settings, tooltip: "" },
];

export function SidebarNav() {
  const pathname = usePathname();

  return (
    <SidebarMenu className="flex flex-col h-full">
      <div className="flex-grow">
        {navItems.map((item) => (
          <SidebarMenuItem key={item.label}>
            <Link href={item.href}>
              <SidebarMenuButton
                variant="default"
                className={cn(
                  "w-full justify-start",
                  pathname === item.href ? "bg-sidebar-accent text-sidebar-accent-foreground" : "hover:bg-sidebar-accent/80"
                )}
                tooltip={item.tooltip}
                isActive={pathname === item.href}
              >
                <item.icon className="mr-2 h-5 w-5" />
                <span>{item.label}</span>
              </SidebarMenuButton>
            </Link>
          </SidebarMenuItem>
        ))}
      </div>
      <div className="mt-auto">
        {secondaryNavItems.map((item) => (
          <SidebarMenuItem key={item.label}>
            <Link href={item.href}>
              <SidebarMenuButton
                variant="default"
                className={cn(
                  "w-full justify-start",
                  pathname === item.href ? "bg-sidebar-accent text-sidebar-accent-foreground" : "hover:bg-sidebar-accent/80"
                )}
                tooltip={item.tooltip}
                isActive={pathname === item.href}
              >
                <item.icon className="mr-2 h-5 w-5" />
                <span>{item.label}</span>
              </SidebarMenuButton>
            </Link>
          </SidebarMenuItem>
        ))}
      </div>
    </SidebarMenu>
  );
}